import React, { useState } from 'react';
import L, { map } from 'leaflet'
import { MapContainer, TileLayer, Marker, Popup, useMapEvents } from "react-leaflet"
import { useDispatch, useSelector } from 'react-redux';
import { saveDataAdd } from '../store/actions';

const myIcon = L.icon({
  iconUrl: 'data:image/png;base64,',
  iconSize: [ 25, 41 ],
  iconAnchor: [ 12.5, 41 ],
  popupAnchor: [ 0, -41 ]
})

function MyComponent() {

  const dispatch = useDispatch()
  const map = useMapEvents({
    click: (location) => {
      const points = location.latlng
      dispatch(saveDataAdd({
        name: "",
        city: "",
        province: "",
        latitude: points.lat,
        longitude: points.lng
      }))
    },
    locationfound: (location) => {
      const points = location.latlng
      dispatch(saveDataAdd({
        name: "",
        city: "",
        province: "",
        latitude: points.lat,
        longitude: points.lng
      }))
    },
  })
  return null
}

function MyMap() {
  const { allData, whatAction } = useSelector(state => state)


  const centerPosition = [ -0.789275, 113.921327 ]




  return (
    <MapContainer style={ { width: whatAction !== "edit" && "100%" } } className="map" center={ centerPosition } zoom={ 5 }
      scrollWheelZoom={ true }
    >
      <TileLayer
        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
      />

      {
        allData && allData.map((point) => (
          <Marker
            key={ point.id }
            position={ [ point.latitude, point.longitude ] }
            icon={ myIcon }
          >
            <Popup>
              { point.city }
            </Popup>
          </Marker>
        ))
      }

      <MyComponent />


    </MapContainer>
  )
}

export default MyMap;